import React from 'react'
import logo from './Logo.png'
import './Header.css'

const Header =() =>{
	return(
		
		<div className='container'>
		<img className='logo' alt='logo' src={logo}/>
		<nav className='box'>
		<ul className='main-nav'>
		<li className='asd' id='s'><a>Home</a></li>
		<li className='asd'><a>About</a></li>
		<li className='asd'><a>Services</a></li>
		<li className='asd'><a>Portfolio</a></li>
		<li className='asd'><a>Blog</a></li>
		<li className='asd'><a>Contact</a></li>
		<li className='asd' ><a>Hire</a></li>
		</ul>
		</nav>
		</div>
		)
}

export default Header;